import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import router from "@/router";

import {
    Button,
    Col,
    Dropdown,
    Form,
    FormModel,
    Icon,
    Input,
    Layout,
    Menu,
    message,
    Modal,
    Row,
    Tabs,
    Tooltip,
    Avatar,
    Badge
} from "ant-design-vue";

Vue.config.productionTip = false

Vue.use(Button);
Vue.use(VueRouter);
Vue.use(Form);
Vue.use(Modal);
Vue.use(FormModel);
Vue.use(Tabs);
Vue.use(Input);
Vue.use(Icon);
Vue.use(Col);
Vue.use(Row);
Vue.use(Layout);
Vue.use(Menu);
Vue.use(Tooltip);
Vue.use(Dropdown);
Vue.use(Avatar);
Vue.use(Badge);

Vue.prototype.$message = message;
Vue.prototype.$info = Modal.info;
Vue.prototype.$success = Modal.success;
Vue.prototype.$error = Modal.error;
Vue.prototype.$warning = Modal.warning;
Vue.prototype.$confirm = Modal.confirm;
Vue.prototype.$destroyAll = Modal.destroyAll;

new Vue({
    render: h => h(App),
    router: router
}).$mount('#app')
